import React from 'react';
import { SliderData } from './SliderData';
import { FaArrowAltCircleRight, FaArrowAltCircleLeft } from 'react-icons/fa';

const ImageSlider = ({ slides }) => {
  const [current, setCurrent] = React.useState(0);
  const length = slides.length;

  const nextSlide = () => {
    setCurrent(current === length - 1 ? 0 : current + 1);
  };

  const prevSlide = () => {
    setCurrent(current === 0 ? length - 1 : current - 1);
  };

  if (!Array.isArray(slides) || slides.length <= 0) {
    return null;
  }
  return (
    <div>
      <div>
        <h1>Todo Momento com você é único!!!</h1>
      </div>
      <div>
        <h1>Eu te AMO 💘</h1>
      </div>
      <section className="slider">
        <FaArrowAltCircleLeft className="left-arrow" onClick={prevSlide} />
        <FaArrowAltCircleRight className="right-arrow" onClick={nextSlide} />
        {SliderData.map((slide, index) => {
          return (
            <div
              className={index === current ? 'slide active' : 'slide'}
              key={index}
            >
              {index === current && (
                <img
                  src={slide.image}
                  alt="amor da minha vida"
                  className="image"
                />
              )}
            </div>
          );
        })}
      </section>
      <br /> <br /> <br /> <br />
      <div>
        <p>Mo,</p>
        <p>
          Cada dia mais você me mostra o tanto que eu tenho sorte de ter você
          comigo.
        </p>
        <p>Você é INCRIVEL, não tenho palavras para descrever</p>
        <p>E eu sei mo que você vai conseguir tudo que você almeja</p>
        <p>Todos os seus sonhos, todos os projetos</p>
        <p>
          Porque você nunca desiste deles, você está sempre lutando, sempre
          correndo atrás de tudo
        </p>
        <p>
          E eu vou estar sempre do seu lado, te apoiando em todas as suas
          decisões, em todos os projetos
        </p>
        <p>
          Eu quero estar sempre melhorando para ser o melhor namorado para você
          e um bom marido no futuro tambem.
        </p>
        <p>
          Mo, você é uma namorada incrível, e eu tenho certeza que vai ser uma
          futura esposa incríevl tambem
        </p>
        <p>Você já está se enforçando pra isso, eu consigo ver</p>
        <p>Não falo iso da boca pra fora mo</p>
        <p>Cada vez mais você está lutando para melhorar</p>
        <p>
          E isso mostra o tanto que você me ama, o tanto que você quer ficar
          comigo
        </p>
        <p>E eu espero muito poder te mostrar tambem</p>
        <p>Você é PERFEITA</p>
        <p>MARAVILHOSA</p>
        <p>DEIDCADA</p>
        <p>CARINHOSA</p>
        <p>AMOROSA</p>
        <p>ESFORÇADA</p>
        <p>DECIDIDA</p>
        <p>ENGRAÇADA</p>
        <p>COMPANHEIRA</p>
        <p>SEMPRE ME APOIA</p>
        <p>ME FAZ BEM</p>
        <p>Uma namorada como essa só eu tenho</p>
        <p>E tenho muito orgulho de dizer que VOCÊ É MINHA NAMORADA</p>
        <p>E espero muito te dar esse orgulho tambem</p>
        <p>Vou estar sempre lutando para cada vez mais te fazer feliz</p>
        <p>Te fazer sentir a princesa que você é na minha vida</p>
        <p>EU TE AMO MUITO 💘💘💘</p>
        <p>E vou te amar cada vez mais mo</p>
        <p>
          Espero te fazer soltar esse sorriso maravilho que eu eu tanto amo.
          😁💕💕
        </p>
      </div>
    </div>
  );
};

export default ImageSlider;
